import $ from 'jquery/dist/jquery.min.js';
import Chart from 'chart.js/dist/Chart.bundle.min.js';
import initMobileMenu from './components/mobile-menu';
import DepartmentDropdown from "./components/department-dropdown";

const colors = [
	'#3fb3d5',
	'#99d744',
	'#7c6fba',
	'#0127a1',
	'#61b99e',
	'#b614d4',
	'#9cb9f5',
	'#d66540',
	'#e6f631',
	'#4624ac',
	'#aa8b48',
	'#528724',
	'#c93590',
	'#883a46',
	'#469d2a'
]

const initChart = () => {
	const $root = $('#chart');
	const $chart = $root.find('.chart_canvas');
	let firms = $root.attr('data-firms');
	let sum = $root.attr('data-sum');

	if (!firms || !sum) {
		throw new Error('Dataset for chart is not defined');
	}

	firms = firms.split(',');
	sum = sum.split(',').map((sumStr => {
		const escaped = sumStr.replace(/\'/g, '');
		return +escaped;
	}));

	const chart = new Chart($chart, {
		type: 'doughnut',
		data: {
			labels: firms,
			datasets: [
				{
					data: sum,
					backgroundColor: colors,
					borderWidth: 1
				}
			]
		},
		options: {
			legend: {
				display: window.matchMedia('(min-width: 601px)').matches
			},
			tooltips: {
				callbacks: {
					label: function(tooltipItem, data) {
						const index = tooltipItem.index;
						const datasetIndex = tooltipItem.datasetIndex;
						let label = data.labels[index] || '';
						let dataItem = data.datasets[datasetIndex].data[index] || null;

						if (label && dataItem) {
							label = `${label}: ${dataItem.toLocaleString('ru')} .`;
						}

						return label;
					}
				}
			}
		}
	});
}

const initDepartments = () => {
	const $departmentDropdowns = $('.department-dropdown');
	const headerHeight = $('.site-header').height();

	if ($departmentDropdowns.length) {
		$departmentDropdowns.each((i, element) => {
			const departmentDropdown = new DepartmentDropdown($(element));
			departmentDropdown.$root.on('event:optionSelected', () => {
				const $disposers = $('.disposers');
				const scrollTop = $disposers.offset().top - headerHeight * 2 + $disposers.height();
				$('html, body').animate({ scrollTop });
			});
		});
	}
};

const initApp = () => {
	$(document).ready(() => {
		initMobileMenu();
		initChart();
		initDepartments();
	});
}

initApp();

