import $ from 'jquery/dist/jquery.min.js';

export default class DepartmentDropdown {
	constructor($root) {
		this.$root = $root;
		this.$trigger = null;
		this.$triggerText = null;
		this.$collapsible = null;
		this.$content = null;
		this.$items = null;

		this.OPTION_SELECTED_EVENT = 'event:optionSelected';

		this.init();
	}

	init() {
		if (!this.$root) {
			return;
		}

		this.$trigger = this.$root.find('.department-dropdown_trigger');
		this.$triggerText = this.$trigger.find('.text-btn');
		this.$collapsible = this.$root.find('.department-dropdown_collapsible');
		this.$content = this.$root.find('.department-dropdown_list');
		this.$items = this.$content.find('.department-dropdown_item');

		this.addEventListeners();
	}

	addEventListeners() {
		this.$trigger.click(() => {
			if (this.$root.hasClass('-opened')) {
				this.close();
			} else {
				this.open();
			}
		});

		this.$items.click((e) => {
			this.selectOption($(e.target));
		});
	}

	open() {
		const contentHeight = this.$content.height();
		this.$collapsible.css({ maxHeight: contentHeight + 'px' });
		this.$root.addClass('-opened');
	}

	close() {
		this.$collapsible.css({ maxHeight: 0 });
		this.$root.removeClass('-opened');
	}

	selectOption($option) {
		this.close();
		this.$triggerText.text($option.text());
		this.$root.trigger(this.OPTION_SELECTED_EVENT);

		const $active = this.$content.find('.department-dropdown_item.-active');
		setTimeout(() => {
			$active.removeClass('-active');
			$option.addClass('-active');
		}, 360);
	}
}